<template>
<v-flex dark xs12>
  <v-row>

    <v-col cols="8" sm="4">
      <v-text-field :ref="id_l" :prepend-icon="delete_icon" @click:prepend="delete_input(index)" @change="input_onchange(id=0,index)" :value="label" solo></v-text-field>
    </v-col>

    <v-col cols="12" sm="8">
      <v-text-field :ref="id_v" :value="value" @change="input_onchange(id_v,index)" solo-inverted></v-text-field>
    </v-col>

  </v-row>

</v-flex>
</template>

<script>
export default {
  props: ["index", "label", "value"],
  data: () => ({
    delete_icon: 'mdi-delete',
    iconIndex: 0,
    icons: [
      'mdi-emoticon',
      'mdi-emoticon-cool',
      'mdi-emoticon-dead',
      'mdi-emoticon-excited',
      'mdi-emoticon-happy',
      'mdi-emoticon-neutral',
      'mdi-emoticon-sad',
      'mdi-emoticon-tongue',
    ],

  }),

  computed: {
    icon() {
      return this.icons[this.iconIndex]
    },
    id_l() {
      return 'l' + this.$props.index;
    },
    id_v() {
      return 'v' + this.$props.index;
    }


  },
  mounted() {
    //console.log(this.data_type)
    console.log("mounted");
    console.log(this.$props.value);
    console.log(this.$store.state.inputs);



    /*

    this.date = this.$store.state.user.user_json_file["date_naissance"];
    if (this.$props.data_type == "info")
      this.set_pdf_data("info", "date_naissance", this.date);
      */
  },
  methods: {
    input_onchange(id, index) {
      console.log('input onchange:', id);
      console.log('input onchange:', index);
      if (id != 0) {
        //const field = this.$refs[id][0].$refs.input._value;
        var value = this.$refs[id].$refs.input._value;
        console.log(value);
        var json = {};
        var id_l = 'l' + index;
        var key = this.$refs[id_l].$refs.input._value;
        json[key] = value;

        var payload = {
          "item": json,
          "index": index
        }

        var input_item = {
          "label": key,
          "value": value
        }
        var input_data = {
          "item": input_item,
          "index": index
        }

        //this.pdf_data[key]= data //JSON.stringify(json)
        this.$store.dispatch("requests/set_outputs", payload);
        this.$store.dispatch("requests/set_inputs", input_data);
      }

    },
    changeIcon() {
      this.iconIndex === this.icons.length - 1 ?
        this.iconIndex = 0 :
        this.iconIndex++
    },

    delete_input(index) {
      console.log('delete input');
      console.log(index)
      this.$store.dispatch("requests/delete_input", index);
    }


  },
};
</script>
<style scoped></style>
