<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      e6: 1,
      step: 1,
    };
  },
  computed: {
    ...mapGetters("requests", ["get_selected_request", "selected_sending"]),
  },
  mounted() {
    //model_types = this.$store.state.model_types
    console.log("hzllo mounted");
    //this.$store.dispatch("get_email_models");
    //this.$store.dispatch("pdf/load_pdf_models");
    this.$store.dispatch("api/api_request_generate");
  },
  methods: {
    send_email() {
      console.log("final action");
    },
