      var pdf_url = this.$store.state.api.pdf_url;
      console.log(pdf_url);
      window.open(pdf_url, "_blank");
    },
    next_step() {
      if (this.step == 3) {
        this.$store.dispatch("api/api_request_add");
        this.$store.dispatch("api/api_pdf_create");
      }
      this.step += 1;
    },
    previous_step() {
      this.step -= 1;
    },
    step2() {
      this.step = 3;
    },
    step3() {
      this.step = 4;
      console.log("step 3 clicked continue...generate PDF");
    },
  },
