<template>
  <div>
    <v-flex dark xs12 v-for="(item, i) in form_inputs" :key="i">
      {{ item.label }}
      <v-dialog
        ref="dialog"
        v-model="modal"
        :return-value.sync="date"
        persistent
        width="290px"
        v-if="item.fieldType === 'datetime'"
      >
        <template v-slot:activator="{ on }">
          <v-text-field
            v-model="date"
            label="Picker in dialog"
            prepend-icon="mdi-calendar"
            readonly
            v-on="on"
          ></v-text-field>
        </template>
        <v-date-picker v-model="date" scrollable>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
          <v-btn
            text
            color="primary"
            @click="save(i, date, data_type, form_data[i])"
            >OK</v-btn
          >
        </v-date-picker>
      </v-dialog>
      <v-textarea
        :ref="form_data[i]"
        @change="input_onchange(data_type, form_data[i])"
        v-if="item.fieldType === 'large'"
        :value="default_value(data_type, form_data[i])"
        filled
      ></v-textarea>
      <v-text-field
        :ref="form_data[i]"
        @change="input_onchange(data_type, form_data[i])"
        v-if="item.fieldType === 'normal'"
        :value="default_value(data_type, form_data[i])"
        filled
      ></v-text-field>
    </v-flex>
  </div>
</template>

<script>
export default {
  props: ["data_type"],
  data: () => ({
    form_inputs: [],
    form_data: [],
    date: new Date().toISOString().substr(0, 10),
    pdf_data: {},
    modal: false,
  }),

  computed: {},
  mounted() {
    //console.log(this.data_type)
    console.log("mounted");
    console.log(this.$props.data_type);
    var type = this.$props.data_type;
    this.$store.dispatch("pdf/get_form_inputs", type).then((res) => {
      this.form_inputs = res;
      this.$store.dispatch("pdf/get_form_data", type).then((res) => {
        console.log("im here get form data...xxxxxxxxxxxxxx");
        this.form_data = res;
        console.log(res);
      });
    });

    /*

    this.date = this.$store.state.user.user_json_file["date_naissance"];
    if (this.$props.data_type == "info")
      this.set_pdf_data("info", "date_naissance", this.date);
      */
  },
  methods: {
    default_value(type, id) {
      console.log("im here default value xxxxxxxxxxxxxxxxxx");
      console.log(type, id);

      var value = "";
      /*
      if (type == "from") {
        value = this.$store.state.user.user_json_file[id];
        this.set_pdf_data("from", id, value);
      }*/
      return value;
    },
    save(index, time, type, key) {
      this.$refs.dialog[index].save(time);
      //this.date = time
      console.log("save date", this.date);

      this.set_pdf_data(type, key, this.date);
    },
    input_onchange(type, id) {
      console.log("input on change000");
      const field = this.$refs[id][0].$refs.input._value;
      //const field = document.querySelector("input[id=to_adress]").value
      console.log(field);

      this.set_pdf_data(type, id, field);
    },
    set_pdf_data(type, key, data) {
      console.log("before:", this.pdf_data);
      var json = {};
      json[key] = data;

      //this.pdf_data[key]= data //JSON.stringify(json)
      this.$store.dispatch("pdf/set_pdf_Data", json).then(() => {
        console.log("after in store state data:");
        console.log(this.$store.state.pdf.pdf_data);
      });
    },
  },
};
</script>
