// https://vuex.vuejs.org/en/getters.html

export default {

  get_request_ref(state) {
    return state.request_ref;
  },

  selected_sending(state) {
    return state.selected_sending;
  },
  selected_wrappper(state) {
    return state.selected_req_type.id
  },
  get_selected_request(state) {
    return state.selected_req_type;
  },
  get_inputs(state) {
    console.log("get_inputs:", state.inputs);
    var len = state.inputs.length;
    if (len == 0) {
      state.inputs = [...state.inputs, ...state.default_inputs];
    }
    return state.inputs
  },
  get_outputs(state) {
    console.log("get_outputs:", state.outputs);
    return state.outputs
  },
  pdf_viewer_url(state) {
    console.log("pdf url xxxxx>>>", state.pdf_viewer_item.url);
    return state.pdf_viewer_item.url;
  },
  pdf_list(state) {
    return state.pdf_list;
  },
  pdf_url(state) {
    return state.pdf_url;
  },
  request_types(state) {
    return state.request_types;
  },
  sending_types(state) {
    return state.sending_types;
  },
  get_to_subtitle(state) {
    return state.pdf_model_form.to.subtitle;
  },

  get_form_labels(state) {
    var pdf_model_form = state.pdf_model_form;
    var labels = [{
      type: "info",
      label: pdf_model_form.info.label
    },
    {
      type: "from",
      label: pdf_model_form.from.label
    },
    {
      type: "to",
      label: pdf_model_form.to.label
    },
    ];
    console.log("im here get form labels.....");
    console.log(labels);
    return labels;
  },
  form_inputs(state, type) {
    console.log("im here getters get_form_inputs.....");
    console.log(type);
    var pdf_model_form = state.pdf_model_form;
    var inputs = {
      info: pdf_model_form.info.inputs,
      to: pdf_model_form.to.inputs,
      from: pdf_model_form.from.inputs,
    };
    console.log(inputs[type]);
    return inputs[type];
  },
  form_data(state, type) {
    console.log("im here getters form_data.....");
    console.log(type);
    var pdf_model_form = state.pdf_model_form;
    var data = {
      info: pdf_model_form.info.data,
      to: pdf_model_form.to.data,
      from: pdf_model_form.from.data,
    };

    return data[type];
  },
};