// https://vuex.vuejs.org/en/actions.html
import { axios } from "@/plugins/axios";
export default {
  //
  api_request_generate({
    state, rootState
  }) {

    var url = state.api_url + state.request_generate
    console.log("api_request_generate:", url)

    axios.get(url).then(({
      data
    }) => {
      console.log(data);
      rootState.requests.request_ref = data
      console.log("request_ref:", rootState.requests.request_ref)



    });
  },
  api_request_add({
    state, rootState
  }) {

    var url = state.api_url + state.request_add_new

    var requests = rootState.requests

    var payload = {
      "ref": requests.request_ref,
      "reqtype": requests.selected_req_type.id,
      "sendingtype": requests.selected_sending.type,
      "inputs": requests.inputs
    }
    console.log("api_request_add:", url)
    console.log("payload:", payload)

    axios.post(url, payload).then(({
      data
    }) => {
      console.log(data);

    });
  },
  api_pdf_create({
    state, rootState
  }) {

    var url = state.api_url + state.pdf_create
    var requests = rootState.requests
    const timeElapsed = Date.now();
    const today = new Date(timeElapsed);

    var payload = {
      "ref": requests.request_ref,
      "date": today.toDateString(),
      "reqtype": requests.selected_req_type.id,
      "sendingtype": requests.selected_sending.type,
      "mydatta": requests.inputs,
      "selectedcompany": requests.selected_company
    }
    console.log("api_pdf_create:", url)
    console.log("payload:", payload)

    axios.post(url, payload).then(({
      data
    }) => {
      console.log(data);
      state.pdf_url = state.api_url + "/" + requests.request_ref + ".pdf"

    });
  },
  api_company_search({
    state, name
  }) {

    var url = state.api_url + state.company_search + name

    console.log("api_request_generate:", url)

    axios.get(url).then(({
      data
    }) => {
      console.log(data);


    });
  },
  get_company_search_url({ state }, val) {
    return state.api_url + state.company_search + val
  },

};
