// https://vuex.vuejs.org/en/actions.html
import {
  axios
} from "@/plugins/axios";
export default {
  //
  set_selected_company({ state }, selected) {
    state.selected_company = selected;
    console.log("selected_company:=>>>>", state.selected_company);
  },
  add_input({
    state
  }, item) {

    state.inputs.push(item);
    console.log("add_input::", state.inputs);
  },

  reset_inputs({
    state
  }) {

    state.inputs = [];
    state.outputs = [];

    state.inputs = [...state.inputs, ...state.default_inputs];
    console.log("default:", state.default_inputs);
    console.log("inputs:", state.inputs);
    console.log('outputs:', state.outputs);
  },
  delete_input({
    state
  }, index) {
    console.log('delete_input', index);
    state.inputs.splice(index, 1);
    state.outputs.splice(index, 1);
    console.log('inputs:', state.inputs);
    console.log('outputs:', state.outputs);
  },

  set_inputs({
    state
  }, data) {

    var inputs_len = state.outputs.length;
    console.log('set_inputs,inputs_len:', inputs_len);
    console.log('set_inputs,data:', data);
    console.log('set_inputs,item:', data.item);
    console.log('set_inputs,index', data.index);

    //store data to localstorage!!
    if (inputs_len <= data.index) {
      state.inputs.push(data.item);
    } else {
      //edit array by index
      state.inputs[data.index] = data.item
    }


    console.log('inputs:', state.inputs);

  },


  set_outputs({
    state

  }, data) {

    var ouputs_len = state.outputs.length;
    console.log('set_outputs,ouputs_len:', ouputs_len);
    console.log('set_outputs,data:', data);
    console.log('set_outputs,item:', data.item);
    console.log('set_outputs,index', data.index);
    //store data to localstorage!!
    if (ouputs_len <= data.index) {
      state.outputs.push(data.item);
    } else {
      //edit array by index
      state.outputs[data.index] = data.item
    }
    console.log('outputs:', state.outputs);





  },
  set_pdf_viewer({
    state,
    dispatch
  }, item) {
    dispatch("app/set_loader", true, {
      root: true
    });
    state.pdf_viewer_item = item;
  },
  pdf_list({
    state
  }, list) {
    var pdfs = [];
    console.log("im here pdf_list from acrionsxxxxxx");

    for (var item of list) {
      var itm = {
        icon: "mdi-file-pdf",
        iconClass: "grey lighten-1 white--text",
        title: item.title,
        size: item.size,
        created_at: item.created_at,
        url: item.url,
      };
      pdfs.push(itm);
    }

    state.pdf_list = pdfs;
    return state.pdf_list;
  },
  generate_pdf({
    state,
    dispatch
  }) {
    console.log("generate PDF xxxxxxxxxxxxxxx");
    console.log(state.pdf_data);
    dispatch("app/set_loader", true, {
      root: true
    });
  },
  load_pdf_models({
    state,
    dispatch
  }) {
    console.log("load_pdf_models");
    dispatch("app/set_loader", true, {
      root: true
    });
    axios.get("/json/pdf_models.json").then(({
      data
    }) => {
      console.log(data);
      state.pdf_models = data;
      dispatch("app/set_loader", false, {
        root: true
      });
    });
  },
  set_selected_type({
    state
  }, value) {
    state.selected_req_type = value;
    console.log(value);
  },
  set_selected_sending({
    state
  }, value) {
    state.selected_sending = value;
    console.log(value);
  },
  get_pdf_template({
    state
  }) {
    var template_link = state.selected_model.template;
    axios.get(template_link).then(({
      data
    }) => {
      console.log(data);
      state.pdf_template = data;
    });
  },
  get_pdf_model_form({
    state
  }) {
    var form_link = state.selected_model.form;
    axios.get(form_link).then(({
      data
    }) => {
      console.log(data);
      state.pdf_model_form = data;
    });
  },
  set_pdf_Data({
    state
  }, data) {
    Object.assign(state.pdf_data, data);
  },
  get_form_labels({
    state
  }) {
    var form = state.pdf_model_form;
    var labels = [{
      type: "info",
      label: form.info.label
    },
    {
      type: "from",
      label: form.from.label
    },
    {
      type: "to",
      label: form.to.label
    },
    ];
    state.form_labels = labels;
  },
  selected({
    dispatch
  }, item) {
    console.log("im here selected item.....");
    console.log(item.title);
    dispatch("set_selected_model", item).then(() => {
      dispatch("get_pdf_template");
      dispatch("get_pdf_model_form");
    });
  },
  get_form_inputs({
    state
  }, type) {
    console.log("im here get_form_inputs.....");
    console.log(type);
    var pdf_model_form = state.pdf_model_form;
    var inputs = {
      info: pdf_model_form.info.inputs,
      to: pdf_model_form.to.inputs,
      from: pdf_model_form.from.inputs,
    };
    console.log(inputs[type]);

    return inputs[type];
  },
  get_form_data({
    state
  }, type) {
    console.log("im here get_form_data.....");
    console.log(type);
    var pdf_model_form = state.pdf_model_form;

    var data = {
      info: pdf_model_form.info.data,
      to: pdf_model_form.to.data,
      from: pdf_model_form.from.data,
    };
    return data[type];
  },
};